import React from "react";
import { Link } from "react-router-dom";

export function Tickets() {
  return (
    <div>
      <h1>[Tickets]</h1>
      <nav>
        <ul>
          <li>
            <Link to="/tours">Home</Link>
          </li>
          <li>
            <Link to="/about">About</Link>
          </li>
          <li>
            <Link to="/tickets">Tickets</Link>
          </li>
        </ul>
      </nav>
    </div>
  );
}
